"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoCopyToClipboard() {
  const [ok, setOk] = useState(false);
  const code = "npx create-interface-app@latest";

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="group rounded-2xl border border-[var(--conv-border)] bg-[var(--conv-surface)] p-6 cursor-pointer hover:border-[var(--conv-accent)] transition-all"
        onClick={(e) => openSpecCard({
            featureKey: "copy-clipboard",
            title: "Terminal Copy",
            tags: ["Utility", "DX"],
            codeUrl: "...",
            anchor: { x: e.clientX, y: e.clientY },
        })}
      >
        <div className="flex items-center justify-between rounded-xl bg-[var(--conv-ink)] p-4 shadow-inner">
          <code className="text-xs font-mono text-indigo-300">{code}</code>
          <button
            className={`px-4 py-2 rounded-lg text-xs font-bold transition-all ${
              ok ? "bg-emerald-500 text-white" : "bg-white/10 text-white hover:bg-white/20"
            }`}
            onClick={async (ev) => {
              ev.stopPropagation();
              await navigator.clipboard.writeText(code);
              setOk(true);
              setTimeout(() => setOk(false), 2000);
            }}
          >
            {ok ? "SUCCESS" : "COPY"}
          </button>
        </div>
      </div>
    </section>
  );
}