"use client";

import React from "react";
import { useLikes, type LikeVariant } from "@/components/likes/LikesContext";

// Map internal keys to friendly display text
const VARIANT_LABELS: Record<LikeVariant, string> = {
  like: "I like it",
  love: "I love this",
  use_it: "Use it",
  almost: "Almost there",
  not_for_me: "Not for me"
};

export default function SummaryForm() {
  const { items } = useLikes();
  
  // Only show items where at least one sentiment variant is selected
  const liked = Object.entries(items).filter(([_, rec]) => rec.variants.size > 0);

  if (!liked.length) {
    return (
      <footer className="max-w-4xl mx-auto px-6 py-32 text-center">
        <div className="h-px w-full bg-gradient-to-r from-transparent via-zinc-200 to-transparent mb-12" />
        <p className="text-zinc-400 font-mono text-[10px] uppercase tracking-[0.4em]">Inventory Empty</p>
      </footer>
    );
  }

  const subject = `Interface Selection: ${liked.length} Items`;

  // Build the email body with the correct sentiment labels
  const bodyText = [
    "Hi Tate,",
    "",
    "I've curated my preferred interface patterns from your collection:",
    "",
    ...liked.map(([key, rec]) => {
      const sentimentList = Array.from(rec.variants)
        .map(v => VARIANT_LABELS[v as LikeVariant])
        .join(", ");
      
      return `• ${rec.title || key}\n  Feedback: ${sentimentList}\n  Source: ${rec.codeUrl || "In-situ"}\n`;
    }),
    "",
    "Let's talk about the implementation details for my project.",
    "--",
    "Sent from tatedonohoe.au",
  ].join("\n");

  const gmail = `https://mail.google.com/mail/?view=cm&fs=1&to=tate@ecodia.au&su=${encodeURIComponent(subject)}&body=${encodeURIComponent(bodyText)}`;

  return (
    <footer className="max-w-5xl mx-auto px-6 py-32 animate-in fade-in slide-in-from-bottom-8 duration-1000">
      <div className="rounded-[3rem] border border-zinc-200 bg-white p-8 md:p-16 shadow-2xl">
        <div className="flex flex-col md:flex-row justify-between items-start gap-12">
          
          <div className="max-w-md">
            <h2 className="text-4xl font-medium tracking-tighter mb-4 text-zinc-900">
              Finalize <span className="italic font-serif text-zinc-400 text-3xl">the</span> Selection
            </h2>
            <p className="text-zinc-500 leading-relaxed mb-8 font-light">
              You’ve tagged {liked.length} components with specific feedback. Send this brief over, and I'll review the technical requirements for your build.
            </p>
            
            <div className="flex flex-wrap gap-4">
              <a
                href={gmail}
                target="_blank"
                className="px-10 py-4 rounded-full bg-zinc-900 text-white text-xs font-bold uppercase tracking-widest hover:bg-zinc-800 transition-all active:scale-95 shadow-xl"
              >
                Send via Gmail
              </a>
              <button
                onClick={() => {
                  navigator.clipboard.writeText(bodyText);
                  // Using a simple alert, but you could use your DemoToast logic here!
                  alert("Brief copied to clipboard.");
                }}
                className="px-10 py-4 rounded-full border border-zinc-200 text-xs font-bold uppercase tracking-widest hover:bg-zinc-50 transition-all text-zinc-600"
              >
                Copy Brief
              </button>
            </div>
          </div>

          {/* THE RECEIPT SIDEBAR */}
          <div className="w-full md:w-80">
            <div className="bg-zinc-50/50 rounded-2xl p-6 border border-zinc-100">
              <span className="text-[10px] font-bold uppercase tracking-[0.2em] text-zinc-400 mb-6 block">Your Manifest</span>
              <div className="space-y-6">
                {liked.map(([key, rec]) => (
                  <div key={key} className="group">
                    <div className="text-sm font-semibold text-zinc-900 mb-1">{rec.title}</div>
                    <div className="flex flex-wrap gap-1">
                      {Array.from(rec.variants).map((v) => (
                        <span 
                          key={v} 
                          className="text-[9px] font-bold uppercase px-2 py-0.5 rounded-full bg-zinc-200 text-zinc-600 border border-zinc-300 transition-colors group-hover:bg-zinc-900 group-hover:text-white"
                        >
                          {VARIANT_LABELS[v as LikeVariant]}
                        </span>
                      ))}
                    </div>
                  </div>
                ))}
              </div>
              <div className="mt-8 pt-6 border-t border-zinc-200 flex justify-between items-center text-zinc-400">
                <span className="text-[10px] uppercase font-bold tracking-widest">Total Assets</span>
                <span className="font-mono text-sm">{liked.length}</span>
              </div>
            </div>
          </div>

        </div>
      </div>
      
      <div className="mt-20 text-center">
        <p className="text-zinc-300 font-mono text-[10px] uppercase tracking-[0.5em]">Tate Donohoe / Production Ready © 2026</p>
      </div>
    </footer>
  );
}