"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

const QA = [
  { q: "Technical Stack?", a: "TypeScript, Next.js, Tailwind CSS, and Framer Motion for high-fidelity interactions." },
  { q: "Scalability?", a: "Architected for performance with edge-ready components and optimized asset delivery." },
  { q: "Delivery Timeline?", a: "Iterative drops. We move from wireframe to production-ready code in rapid cycles." },
];

export function DemoAccordionFAQ() {
  const [open, setOpen] = useState<number | null>(0);
  return (
    <section className="mx-auto max-w-3xl px-6">
      <div className="space-y-4">
        {QA.map((row, i) => (
          <div key={i} className={`rounded-2xl border transition-all duration-300 ${open === i ? "border-[var(--conv-accent)] bg-white shadow-lg" : "border-[var(--conv-border)] bg-[var(--conv-surface)]"}`}>
            <button
              className="w-full flex items-center justify-between px-6 py-5 text-left group"
              onClick={(e) => {
                setOpen((v) => (v === i ? null : i));
                openSpecCard({
                  featureKey: "accordion-faq",
                  title: "Disclosure UI",
                  tags: ["FAQ", "UX", "Content"],
                  codeUrl: "...",
                  anchor: { x: e.clientX, y: e.clientY },
                });
              }}
            >
              <span className={`font-semibold transition-colors ${open === i ? "text-[var(--conv-ink)]" : "text-[var(--conv-dim)] group-hover:text-[var(--conv-ink)]"}`}>{row.q}</span>
              <div className={`h-6 w-6 rounded-full border border-[var(--conv-border)] flex items-center justify-center transition-transform duration-300 ${open === i ? "rotate-45" : ""}`}>+</div>
            </button>
            <div className={`grid transition-all duration-300 ease-in-out ${open === i ? "grid-rows-[1fr] opacity-100" : "grid-rows-[0fr] opacity-0"}`}>
              <div className="overflow-hidden">
                <p className="px-6 pb-6 text-sm leading-relaxed text-[var(--conv-dim)]">{row.a}</p>
              </div>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
}