"use client";

import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoMasonryGrid() {
  const items = Array.from({ length: 12 }).map((_, i) => ({
    id: i,
    h: 120 + ((i * 43) % 180),
  }));

  return (
    <section className="mx-auto max-w-6xl px-6">
      <div className="mb-8">
         <h2 className="text-sm font-medium text-zinc-400 uppercase tracking-widest">Fluid Layouts</h2>
      </div>
      <div
        className="rounded-[2rem] border border-zinc-100 bg-white/30 p-8 backdrop-blur-sm cursor-pointer"
        onClick={(e) =>
          openSpecCard({
            featureKey: "masonry-grid",
            title: "MasonryGrid",
            tags: ["dynamic", "layout", "fluid"],
            codeUrl: "https://github.com/your/repo/path/DemoMasonryGrid.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <div className="columns-2 md:columns-4 gap-6 [column-fill:_balance]">
          {items.map((it) => (
            <div
              key={it.id}
              className="mb-6 break-inside-avoid rounded-2xl border border-zinc-100 bg-white shadow-sm hover:shadow-md transition-shadow duration-500"
              style={{ height: it.h }}
            >
              <div className="p-4 flex gap-2">
                <div className="w-2 h-2 rounded-full bg-zinc-100" />
                <div className="w-8 h-2 rounded-full bg-zinc-50" />
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}