"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoToast() {
  const [show, setShow] = useState(false);
  return (
    <section className="mx-auto max-w-5xl px-6">
      <div className="flex flex-col gap-4 p-8 rounded-[2rem] border border-[var(--conv-border)] bg-[var(--conv-surface)]">
        <h2 className="text-xs uppercase tracking-widest text-[var(--conv-dim)] font-bold">Feedback Loop</h2>
        <button
          className="w-fit px-6 py-3 rounded-xl bg-[var(--conv-ink)] text-white text-sm font-medium hover:shadow-lg transition-all active:scale-95"
          onClick={(e) => {
            setShow(true);
            setTimeout(() => setShow(false), 2500);
            openSpecCard({
              featureKey: "toast",
              title: "Push Notification",
              tags: ["feedback", "system", "motion"],
              codeUrl: "...",
              anchor: { x: e.clientX, y: e.clientY },
            });
          }}
        >
          Send System Message
        </button>
      </div>

      {show && (
        <div className="fixed bottom-10 left-1/2 -translate-x-1/2 z-50 animate-in fade-in slide-in-from-bottom-4 duration-300">
          <div className="flex items-center gap-3 rounded-full bg-[var(--conv-ink)] text-white text-sm pl-2 pr-6 py-2 shadow-2xl border border-white/10">
            <div className="h-8 w-8 rounded-full bg-[var(--conv-accent)] flex items-center justify-center">✓</div>
            <span className="font-medium">Selection saved to your inventory.</span>
          </div>
        </div>
      )}
    </section>
  );
}