"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

const steps = ["Strategy", "Creation", "Refinement", "Handoff"];

export function DemoStepper() {
  const [i, setI] = useState(1);

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="rounded-[2rem] border border-[var(--conv-border)] bg-white p-10 cursor-pointer shadow-sm"
        onClick={(e) => openSpecCard({
            featureKey: "stepper",
            title: "Process Stepper",
            tags: ["UX", "Flow"],
            codeUrl: "...",
            anchor: { x: e.clientX, y: e.clientY },
        })}
      >
        <div className="flex items-center justify-between mb-12">
          {steps.map((s, idx) => (
            <React.Fragment key={s}>
              <div className="flex flex-col items-center gap-3 relative z-10">
                <div className={`h-10 w-10 rounded-full border-2 grid place-items-center text-sm font-bold transition-all duration-500 ${idx <= i ? "bg-[var(--conv-ink)] border-[var(--conv-ink)] text-white" : "bg-white border-[var(--conv-border)] text-[var(--conv-dim)]"}`}>
                  {idx + 1}
                </div>
                <span className={`text-xs font-bold uppercase tracking-tighter ${idx === i ? "text-[var(--conv-ink)]" : "text-[var(--conv-dim)]"}`}>{s}</span>
              </div>
              {idx < steps.length - 1 && (
                <div className="flex-1 h-[2px] bg-[var(--conv-border)] mx-4 -mt-7 overflow-hidden">
                   <div className="h-full bg-[var(--conv-accent)] transition-all duration-700" style={{ width: idx < i ? "100%" : "0%" }} />
                </div>
              )}
            </React.Fragment>
          ))}
        </div>
        <div className="flex gap-3">
          <button className="px-6 py-2 rounded-lg border border-[var(--conv-border)] text-sm font-medium hover:bg-white active:scale-95 transition-all" onClick={(ev) => { ev.stopPropagation(); setI(v => Math.max(0, v - 1)); }}>Back</button>
          <button className="px-6 py-2 rounded-lg bg-[var(--conv-ink)] text-white text-sm font-medium hover:opacity-90 active:scale-95 transition-all" onClick={(ev) => { ev.stopPropagation(); setI(v => Math.min(steps.length - 1, v + 1)); }}>Next Step</button>
        </div>
      </div>
    </section>
  );
}