"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoTooltip() {
  const [show, setShow] = useState(false);
  return (
    <section className="mx-auto max-w-5xl px-6">
      <div className="p-8 rounded-[2rem] bg-white border border-[var(--conv-border)] text-center">
        <div
          className="inline-block relative"
          onMouseEnter={(e) => {
            setShow(true);
            openSpecCard({
              featureKey: "tooltip",
              title: "Contextual Hint",
              tags: ["UX", "Helper"],
              codeUrl: "...",
              anchor: { x: e.clientX, y: e.clientY },
            });
          }}
          onMouseLeave={() => setShow(false)}
        >
          <span className="text-[var(--conv-dim)] cursor-help border-b border-dotted border-[var(--conv-dim)] pb-0.5 hover:text-[var(--conv-accent)] hover:border-[var(--conv-accent)] transition-all">
            What is semantic precision?
          </span>
          {show && (
            <div className="absolute left-1/2 -translate-x-1/2 bottom-full mb-3 w-48 p-3 rounded-lg bg-[var(--conv-ink)] text-white text-[10px] leading-relaxed shadow-xl animate-in fade-in slide-in-from-bottom-2">
              The art of using the correct HTML elements for the job, ensuring accessibility and SEO.
              <div className="absolute top-full left-1/2 -translate-x-1/2 border-8 border-transparent border-t-[var(--conv-ink)]" />
            </div>
          )}
        </div>
      </div>
    </section>
  );
}