"use client";
import React, { useEffect, useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoStickyCTA() {
  const [show, setShow] = useState(false);
  useEffect(() => {
    const onScroll = () => setShow(window.scrollY > 400);
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  return (
    <>
      <section className="mx-auto max-w-3xl px-6 py-20 text-center">
        <h2 className="text-sm font-bold uppercase tracking-[0.3em] text-zinc-400 mb-4">Bottom of page</h2>
        <p className="text-zinc-500 italic font-serif">Keep scrolling to see the floating intent...</p>
      </section>

      <div className={`fixed bottom-8 left-1/2 -translate-x-1/2 z-[80] transition-all duration-500 ease-out ${
        show ? "translate-y-0 opacity-100" : "translate-y-20 opacity-0"
      }`}>
        <div
          className="flex items-center gap-6 rounded-full border border-white/20 bg-black/80 backdrop-blur-xl px-2 py-2 pr-8 shadow-[0_20px_50px_rgba(0,0,0,0.3)] cursor-pointer group hover:scale-105 transition-transform"
          onClick={(e) =>
            openSpecCard({
              featureKey: "sticky-cta",
              title: "Floating Intent",
              tags: ["conversion", "scroll", "sticky"],
              codeUrl: "https://github.com/your/repo/path/StickyCTA.tsx",
              anchor: { x: e.clientX, y: e.clientY },
            })
          }
        >
          <div className="h-10 w-10 rounded-full bg-white flex items-center justify-center text-black font-bold group-hover:bg-[var(--lab-accent)] transition-colors">
            →
          </div>
          <span className="text-white text-sm font-bold uppercase tracking-widest">Let’s build it</span>
        </div>
      </div>
    </>
  );
}