"use client";
import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoKPIStatsStrip() {
  const stats = [
    { k: "Performance", v: "100", s: "LightHouse" },
    { k: "Conversion", v: "+24%", s: "Avg Lift" },
    { k: "Response", v: "12ms", s: "Edge Latency" },
    { k: "Uptime", v: "99.9%", s: "SLA Guaranteed" },
  ];
  return (
    <section className="mx-auto max-w-6xl px-6">
      <div
        className="rounded-[2rem] border border-[var(--conv-border)] bg-[var(--conv-surface)] p-2 cursor-pointer group"
        onClick={(e) => openSpecCard({
            featureKey: "kpi-stats-strip",
            title: "Metrics Dashboard",
            tags: ["Data", "Charts"],
            codeUrl: "...",
            anchor: { x: e.clientX, y: e.clientY },
        })}
      >
        <div className="grid grid-cols-2 md:grid-cols-4 gap-2">
          {stats.map((s) => (
            <div key={s.k} className="bg-white rounded-[1.5rem] p-8 border border-transparent group-hover:border-[var(--conv-border)] transition-all hover:scale-[1.02] hover:shadow-lg">
              <div className="text-[10px] uppercase tracking-widest font-bold text-[var(--conv-dim)] mb-1">{s.k}</div>
              <div className="text-3xl font-bold text-[var(--conv-ink)]">{s.v}</div>
              <div className="text-[10px] text-[var(--conv-accent)] font-medium mt-1">{s.s}</div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}