"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoInlineEditor() {
  const [editing, setEditing] = useState(false);
  const [text, setText] = useState("Click to refine this statement.");

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="rounded-2xl border border-[var(--conv-border)] bg-[var(--conv-surface)] p-8 cursor-pointer hover:shadow-sm transition-all"
        onClick={(e) => openSpecCard({
            featureKey: "inline-editor",
            title: "Dynamic Editor",
            tags: ["Input", "Forms", "UX"],
            codeUrl: "...",
            anchor: { x: e.clientX, y: e.clientY },
        })}
      >
        <div className="flex justify-between items-center mb-6">
           <h2 className="text-xs uppercase tracking-widest text-[var(--conv-dim)] font-semibold">Live Content Modification</h2>
           <div className="h-2 w-2 rounded-full bg-orange-400 animate-pulse" />
        </div>
        <div className="mt-4">
          {!editing ? (
            <button
              className="text-left w-full rounded-xl border border-[var(--conv-border)] bg-white px-6 py-4 text-lg text-[var(--conv-ink)] hover:border-[var(--conv-accent)] transition-colors group flex justify-between items-center"
              onClick={(ev) => { ev.stopPropagation(); setEditing(true); }}
            >
              <span>{text}</span>
              <span className="text-[10px] text-[var(--conv-dim)] opacity-0 group-hover:opacity-100 transition-opacity">EDIT</span>
            </button>
          ) : (
            <div className="flex gap-2 animate-in fade-in slide-in-from-top-1">
              <input
                autoFocus
                className="flex-1 rounded-xl border-2 border-[var(--conv-accent)] px-6 py-4 text-lg outline-none shadow-lg"
                value={text}
                onChange={(e) => setText(e.target.value)}
              />
              <button 
                className="px-6 py-4 rounded-xl bg-[var(--conv-ink)] text-white font-medium hover:bg-black transition-all" 
                onClick={(ev) => { ev.stopPropagation(); setEditing(false); }}
              >
                Save
              </button>
            </div>
          )}
        </div>
      </div>
    </section>
  );
}