"use client";

import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoPhotographyGallery() {
  const imgs = ["/next.svg", "/vercel.svg", "/window.svg", "/globe.svg", "/file.svg", "/next.svg"];
  
  return (
    <section className="mx-auto max-w-6xl px-6">
      <div className="flex justify-between items-end mb-10">
        <div>
          <h2 className="text-2xl font-light tracking-tight">Gallery View</h2>
          <p className="text-zinc-400 text-sm mt-1">Adaptive image architecture</p>
        </div>
        <button className="text-xs uppercase tracking-widest text-zinc-400 border-b border-zinc-200 pb-1">
          View All
        </button>
      </div>
      <div className="grid grid-cols-2 md:grid-cols-3 gap-6">
        {imgs.map((src, i) => (
          <figure
            key={i}
            className="group aspect-[3/4] rounded-2xl border border-zinc-100 bg-zinc-50/50 flex flex-col items-center justify-center cursor-pointer overflow-hidden transition-all hover:shadow-2xl hover:-translate-y-2"
            onClick={(e) =>
              openSpecCard({
                featureKey: "photo-grid",
                title: "PhotographyGallery",
                tags: ["visuals", "grid", "framing"],
                codeUrl: "https://github.com/your/repo/path/PhotographyGallery.tsx",
                anchor: { x: e.clientX, y: e.clientY },
              })
            }
          >
            <img 
              src={src} 
              alt="" 
              className="h-10 opacity-20 group-hover:opacity-100 group-hover:scale-110 transition-all duration-700 grayscale group-hover:grayscale-0" 
            />
            <div className="mt-4 opacity-0 group-hover:opacity-100 transition-opacity duration-500">
               <span className="text-[10px] uppercase tracking-widest text-zinc-400 font-medium">Asset 00{i+1}</span>
            </div>
          </figure>
        ))}
      </div>
    </section>
  );
}