"use client";
import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoMarqueeBanner() {
  return (
    <section className="py-12 bg-black overflow-hidden select-none">
      <div
        className="cursor-pointer group"
        onClick={(e) =>
          openSpecCard({
            featureKey: "marquee-banner",
            title: "Dynamic Marquee",
            tags: ["announcements", "motion", "brand"],
            codeUrl: "https://github.com/your/repo/path/MarqueeBanner.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <div className="flex whitespace-nowrap animate-[marquee_20s_linear_infinite] group-hover:[animation-play-state:paused]">
          {[1, 2, 3, 4].map((_, i) => (
            <div key={i} className="flex items-center text-4xl md:text-6xl font-black tracking-tighter text-white uppercase italic">
              <span className="mx-8">New slots opening next week</span>
              <span className="mx-8 text-zinc-700">/</span>
              <span className="mx-8">Ownership-first web apps</span>
              <span className="mx-8 text-zinc-700">/</span>
              <span className="mx-8 text-transparent stroke-white" style={{ WebkitTextStroke: "1px white" }}>Zero template lock-in</span>
              <span className="mx-8 text-zinc-700">/</span>
            </div>
          ))}
        </div>
      </div>
      <style>{`
        @keyframes marquee {
          0% { transform: translateX(0); }
          100% { transform: translateX(-50%); }
        }
      `}</style>
    </section>
  );
}