"use client";

import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoMinimalistLanding() {
  return (
    <section className="mx-auto max-w-6xl px-6">
      <div
        className="relative group overflow-hidden rounded-[3rem] bg-white border border-zinc-100 shadow-[0_40px_100px_-20px_rgba(0,0,0,0.05)] transition-all duration-700 hover:shadow-[0_40px_120px_-10px_rgba(0,0,0,0.08)] cursor-crosshair"
        onClick={(e) =>
          openSpecCard({
            featureKey: "minimalist-landing",
            title: "Architectural Landing",
            tags: ["High-Fidelity", "Minimalism", "Layout"],
            codeUrl: "https://github.com/your/repo/path/MinimalistLanding.tsx",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        {/* Background Decorative Grid - Now slightly more visible */}
        <div className="absolute inset-0 opacity-[0.05] pointer-events-none [mask-image:radial-gradient(ellipse_at_center,black,transparent)]">
          <div 
            className="absolute inset-0 h-full w-full" 
            style={{ backgroundImage: 'linear-gradient(to right, #888 1px, transparent 1px), linear-gradient(to bottom, #888 1px, transparent 1px)', backgroundSize: '40px 40px' }} 
          />
        </div>

        <div className="relative px-8 py-24 md:py-32 flex flex-col items-center text-center">
          {/* Section ID Tag */}
          <div className="mb-8 flex items-center gap-3">
            <span className="h-[1px] w-8 bg-zinc-200" />
            <span className="text-[10px] font-bold uppercase tracking-[0.4em] text-zinc-400">Inventory No. 004</span>
            <span className="h-[1px] w-8 bg-zinc-200" />
          </div>

          <h2 className="max-w-4xl text-5xl md:text-8xl font-medium tracking-tight text-zinc-900 leading-[0.9] mb-10">
            Buildings for the <br />
            <span className="font-serif italic text-zinc-300">Digital Age.</span>
          </h2>

          <p className="max-w-xl text-lg md:text-xl text-zinc-500 font-light leading-relaxed mb-12">
            We don't just build sites; we architect digital infrastructure that 
            carries the weight of your ambition without breaking a sweat.
          </p>

          <div className="flex flex-col sm:flex-row items-center gap-8">
            <button className="relative px-12 py-4 rounded-full bg-zinc-900 text-white text-xs font-bold uppercase tracking-widest overflow-hidden transition-all hover:scale-105 hover:bg-black active:scale-95 group/btn shadow-2xl">
              <span className="relative z-10">Start the Build</span>
              {/* Subtle hover glow inside button */}
              <div className="absolute inset-0 bg-gradient-to-r from-indigo-500 to-purple-500 opacity-0 group-hover/btn:opacity-20 transition-opacity" />
            </button>
            
            <button className="group flex items-center gap-3 text-xs font-bold uppercase tracking-widest text-zinc-400 hover:text-zinc-900 transition-colors">
              Read the Manifesto
              <span className="h-6 w-6 rounded-full border border-zinc-200 flex items-center justify-center group-hover:border-zinc-900 transition-all group-hover:translate-y-1">
                ↓
              </span>
            </button>
          </div>
        </div>

        {/* Technical Metadata Overlays */}
        <div className="absolute top-10 right-10 hidden lg:block select-none">
           <div className="text-[10px] font-mono text-zinc-300 leading-tight text-right">
             PROJ_TYPE: LANDING_H1<br />
             FRAMEWORK: NEXT_15<br />
             STATUS: PRODUCTION_READY
           </div>
        </div>
        
        <div className="absolute bottom-10 left-10 hidden lg:block select-none">
           <div className="text-[10px] font-mono text-zinc-300 leading-tight">
             COORD: 27.4698° S, 153.0251° E<br />
             LOC: BRISBANE_AU
           </div>
        </div>
      </div>
    </section>
  );
}   