"use client";
import React, { useEffect, useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoSkeletonLoader() {
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    const t = setInterval(() => setLoading(v => !v), 3000);
    return () => clearInterval(t);
  }, []);

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="rounded-2xl border border-[var(--lab-border)] bg-[var(--lab-surface)] p-8 cursor-pointer relative overflow-hidden"
        onClick={(e) => openSpecCard({
            featureKey: "skeleton-loader",
            title: "Ghost UI",
            tags: ["UX", "Loading", "Polish"],
            codeUrl: "...",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <div className="space-y-6">
          {loading ? (
            <div className="space-y-4">
              <div className="h-4 w-1/3 rounded bg-[var(--lab-border)] animate-pulse" />
              <div className="h-32 rounded-xl bg-[var(--lab-border)] animate-pulse flex items-center justify-center">
                 <div className="w-12 h-12 rounded-full border-2 border-t-[var(--lab-accent)] border-[var(--lab-bg)] animate-spin" />
              </div>
              <div className="h-4 w-full rounded bg-[var(--lab-border)] animate-pulse" />
            </div>
          ) : (
            <div className="animate-in fade-in duration-700">
              <div className="text-xs font-mono text-[var(--lab-accent)] mb-4">DATA_RECEIVED</div>
              <div className="rounded-xl border border-[var(--lab-border)] bg-black/40 p-6 font-light text-zinc-400">
                Encrypted payload successfully decrypted. Visualizing interface layers...
              </div>
            </div>
          )}
        </div>
      </div>
    </section>
  );
}