"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoModal() {
  const [open, setOpen] = useState(false);
  return (
    <section className="mx-auto max-w-5xl px-6">
      <button
        className="w-full h-32 rounded-[2rem] border-2 border-dashed border-[var(--conv-border)] hover:border-[var(--conv-accent)] hover:bg-[var(--conv-surface)] transition-all flex flex-col items-center justify-center gap-2 group"
        onClick={(e) => {
          setOpen(true);
          openSpecCard({
            featureKey: "modal",
            title: "Dialog Overlay",
            tags: ["Interstitials", "UX"],
            codeUrl: "...",
            anchor: { x: e.clientX, y: e.clientY },
          });
        }}
      >
        <span className="text-xs font-bold uppercase tracking-widest text-[var(--conv-dim)] group-hover:text-[var(--conv-accent)]">Launch Focus Mode</span>
      </button>

      {open && (
        <div className="fixed inset-0 z-[100] grid place-items-center p-4">
          <div className="absolute inset-0 bg-slate-900/60 backdrop-blur-md animate-in fade-in duration-500" onClick={() => setOpen(false)} />
          <div className="relative rounded-[2.5rem] bg-white p-12 w-full max-w-md shadow-2xl animate-in zoom-in-95 duration-300">
            <h3 className="text-2xl font-bold text-[var(--conv-ink)] mb-4">Deep Focus</h3>
            <p className="text-[var(--conv-dim)] leading-relaxed mb-8">This pattern is designed to eliminate noise, forcing the user to engage with a single, high-intent action.</p>
            <button 
              className="w-full py-4 rounded-xl bg-[var(--conv-ink)] text-white font-bold hover:bg-black transition-all"
              onClick={() => setOpen(false)}
            >
              Continue Exploration
            </button>
          </div>
        </div>
      )}
    </section>
  );
}