"use client";
import React, { useState } from "react";

type ColKey = "todo" | "doing" | "done";
type Card = { id: string; text: string };
type DragData = { id: string; from: ColKey };

/** Tiny runtime check so JSON.parse doesn't give us `any` */
function parseDragData(s: string): DragData | null {
  try {
    const obj = JSON.parse(s) as Partial<DragData>;
    if (
      obj &&
      typeof obj.id === "string" &&
      (obj.from === "todo" || obj.from === "doing" || obj.from === "done")
    ) {
      return obj as DragData;
    }
  } catch {
    /* ignore */
  }
  return null;
}

export function DemoDragDropBoard() {
  const [cols, setCols] = useState<Record<ColKey, Card[]>>({
    todo: [
      { id: "1", text: "Refactor API" },
      { id: "2", text: "Audit Security" },
    ],
    doing: [{ id: "3", text: "UI Polish" }],
    done: [{ id: "4", text: "Define Specs" }],
  });

  const onDragStart = (
    e: React.DragEvent<HTMLDivElement>,
    id: string,
    from: ColKey,
  ) => {
    const payload: DragData = { id, from };
    e.dataTransfer.setData("application/json", JSON.stringify(payload));
  };

  const onDrop = (e: React.DragEvent<HTMLDivElement>, to: ColKey) => {
    e.preventDefault();
    const raw = e.dataTransfer.getData("application/json");
    const data = parseDragData(raw);
    if (!data) return;
    const { id, from } = data;
    if (from === to) return;

    setCols((prev) => {
      const fromList = prev[from];
      const toList = prev[to];

      const card = fromList.find((c: Card) => c.id === id);
      if (!card) return prev; // nothing to move

      return {
        ...prev,
        [from]: fromList.filter((c: Card) => c.id !== id),
        [to]: [...toList, card],
      };
    });
  };

  const colOrder = Object.keys(cols) as ColKey[];

  return (
    <section className="mx-auto max-w-6xl px-6">
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {colOrder.map((k: ColKey) => (
          <div
            key={k}
            className="rounded-2xl border border-[var(--lab-border)] bg-black/20 p-4 min-h-[250px] transition-colors hover:bg-black/40"
            onDragOver={(e) => e.preventDefault()}
            onDrop={(e) => onDrop(e, k)}
          >
            <div className="flex items-center gap-2 mb-4">
              <div className="w-1.5 h-1.5 rounded-full bg-[var(--lab-accent)]" />
              <h4 className="text-[10px] font-mono uppercase tracking-[0.2em] text-[var(--lab-dim)]">
                {k}
              </h4>
            </div>
            <div className="space-y-3">
              {cols[k].map((c: Card) => (
                <div
                  key={c.id}
                  draggable
                  onDragStart={(e) => onDragStart(e, c.id, k)}
                  className="rounded-xl border border-[var(--lab-border)] bg-[var(--lab-surface)] p-4 text-xs font-mono cursor-grab active:cursor-grabbing hover:border-[var(--lab-accent)] transition-all"
                >
                  {c.text}
                </div>
              ))}
            </div>
          </div>
        ))}
      </div>
    </section>
  );
}
