"use client";
import React, { useEffect, useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoGlitchBanner() {
  const [on, setOn] = useState(false);
  useEffect(() => {
    const t = setInterval(() => setOn((v) => !v), 150);
    return () => clearInterval(t);
  }, []);

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="rounded-2xl border border-[var(--lab-border)] bg-[var(--lab-surface)] p-1 overflow-hidden cursor-pointer"
        onClick={(e) => openSpecCard({
            featureKey: "glitch-banner",
            title: "Glitch Engine",
            tags: ["Motion", "Brutalist", "Visuals"],
            codeUrl: "...",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <div className="relative rounded-xl bg-black p-12 overflow-hidden">
          <div className={`text-4xl md:text-6xl font-black italic tracking-tighter transition-all duration-75 
            ${on ? "translate-x-1 skew-x-3 text-red-500" : "-translate-x-1 -skew-x-3 text-cyan-400"}`}>
            INTERFACE_ERROR
          </div>
          <div className="absolute inset-0 flex items-center justify-center mix-blend-screen opacity-50">
             <div className={`text-4xl md:text-6xl font-black italic tracking-tighter text-white ${on ? "block" : "hidden"}`}>
               INTERFACE_ERROR
             </div>
          </div>
          <p className="mt-4 font-mono text-[10px] text-[var(--lab-dim)] uppercase tracking-[0.3em]">Critical System Flux</p>
        </div>
      </div>
    </section>
  );
}