"use client";
import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoPricingMatrix() {
  const tiers = [
    { name: "MVP", price: "$2.5k", points: ["3 Page Prototype", "Mobile Responsive", "Ready in 7 Days"], accent: false },
    { name: "Full Build", price: "$6k", points: ["Complete Web App", "Custom CMS", "Motion System"], accent: true },
    { name: "Retainer", price: "Custom", points: ["Unlimited Updates", "Priority Support", "Strategy Sync"], accent: false },
  ];
  
  return (
    <section className="mx-auto max-w-6xl px-6">
      <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
        {tiers.map((t) => (
          <div
            key={t.name}
            className={`relative rounded-[2rem] border p-10 transition-all duration-500 cursor-pointer ${t.accent ? "border-[var(--conv-accent)] bg-white shadow-2xl scale-105 z-10" : "border-[var(--conv-border)] bg-[var(--conv-surface)] hover:bg-white"}`}
            onClick={(e) => openSpecCard({
                featureKey: "pricing-matrix",
                title: "Pricing Architecture",
                tags: ["Sales", "Grid"],
                codeUrl: "...",
                anchor: { x: e.clientX, y: e.clientY },
            })}
          >
            {t.accent && <div className="absolute top-0 left-1/2 -translate-x-1/2 -translate-y-1/2 bg-[var(--conv-accent)] text-white text-[10px] font-bold uppercase tracking-widest px-4 py-1 rounded-full">Most Popular</div>}
            <div className="text-xs font-bold uppercase tracking-[0.2em] text-[var(--conv-dim)] mb-2">{t.name}</div>
            <div className="text-4xl font-bold text-[var(--conv-ink)] mb-8">{t.price}</div>
            <ul className="space-y-4 mb-10">
              {t.points.map((p) => (
                <li key={p} className="flex items-center gap-3 text-sm text-[var(--conv-dim)]">
                  <div className="h-1.5 w-1.5 rounded-full bg-[var(--conv-accent)]" /> {p}
                </li>
              ))}
            </ul>
            <button className={`w-full py-4 rounded-xl font-bold text-sm transition-all ${t.accent ? "bg-[var(--conv-accent)] text-white hover:opacity-90" : "bg-white border border-[var(--conv-border)] text-[var(--conv-ink)] hover:border-[var(--conv-ink)]"}`}>Get Started</button>
          </div>
        ))}
      </div>
    </section>
  );
}