"use client";

import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoScrollingCards() {
  return (
    <section className="mx-auto max-w-6xl px-6">
      <h2 className="text-xl font-light mb-8 text-zinc-500">Service Categories</h2>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
        {Array.from({ length: 6 }).map((_, i) => (
          <article
            key={i}
            onClick={(e) =>
              openSpecCard({
                featureKey: "scrolling-cards",
                title: "ScrollingCards",
                codeUrl: "https://github.com/your/repo/path/ScrollingCards.tsx",
                tags: ["cards", "elevation", "hover"],
                anchor: { x: e.clientX, y: e.clientY },
              })
            }
            className="group relative aspect-square rounded-[2rem] border border-zinc-100 bg-white p-8 cursor-pointer overflow-hidden transition-all duration-500 hover:shadow-[0_30px_60px_-15px_rgba(0,0,0,0.1)]"
          >
            <div className="relative z-10">
              <span className="text-zinc-300 text-5xl font-serif italic">0{i+1}</span>
              <h3 className="text-xl font-medium mt-4 text-zinc-900 transition-colors group-hover:text-black">Module Type</h3>
              <p className="text-sm text-zinc-400 mt-2 font-light leading-relaxed">
                Detailed exploration of specific interface patterns and their psychological impact on users.
              </p>
            </div>
            {/* Soft decorative element that appears on hover */}
            <div className="absolute -bottom-4 -right-4 w-32 h-32 bg-zinc-50 rounded-full group-hover:scale-150 transition-transform duration-700 ease-out" />
          </article>
        ))}
      </div>
    </section>
  );
}