"use client";
import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoOSTechFrame() {
  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="rounded-xl border border-[var(--lab-border)] bg-[var(--lab-surface)] overflow-hidden shadow-[var(--shadow-tech)] group transition-all hover:border-[var(--lab-accent)]"
        onMouseEnter={(e) =>
          openSpecCard({
            featureKey: "os-tech-frame",
            title: "System Window",
            tags: ["OS-UI", "Chrome", "Technical"],
            codeUrl: "...",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <div className="flex items-center gap-2 px-4 py-3 border-b border-[var(--lab-border)] bg-white/5">
          <div className="flex gap-1.5">
            <span className="h-3 w-3 rounded-full bg-[#ff5f56]" />
            <span className="h-3 w-3 rounded-full bg-[#ffbd2e]" />
            <span className="h-3 w-3 rounded-full bg-[#27c93f]" />
          </div>
          <span className="ml-4 font-mono text-[10px] tracking-widest uppercase text-[var(--lab-dim)] group-hover:text-[var(--lab-accent)] transition-colors">
            root_directory / interface.sys
          </span>
        </div>
        <div className="p-8 font-mono text-sm leading-relaxed text-[var(--lab-dim)]">
          <span className="text-[var(--lab-accent)]">$</span> Initializing system frame...<br/>
          <span className="text-[var(--lab-accent)]">$</span> Mounting skeuomorphic controls.<br/>
          <span className="text-[var(--lab-accent)]">$</span> <span className="animate-pulse inline-block w-2 h-4 bg-[var(--lab-accent)] align-middle" />
        </div>
      </div>
    </section>
  );
}