"use client";
import React, { useEffect, useMemo, useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

const ACTIONS = [
  { id: "open", label: "Search Components", key: "S" },
  { id: "export", label: "Export Source Code", key: "E" },
  { id: "theme", label: "Switch Lab Theme", key: "T" },
];

export function DemoCommandPalette() {
  const [open, setOpen] = useState(false);
  const [q, setQ] = useState("");

  useEffect(() => {
    const onKey = (e: KeyboardEvent) => {
      if ((e.ctrlKey || e.metaKey) && e.key === "k") { e.preventDefault(); setOpen(v => !v); }
      if (e.key === "Escape") setOpen(false);
    };
    window.addEventListener("keydown", onKey);
    return () => window.removeEventListener("keydown", onKey);
  }, []);

  const filtered = useMemo(() => {
    return ACTIONS.filter(a => a.label.toLowerCase().includes(q.toLowerCase()));
  }, [q]);

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div 
        className="p-10 rounded-2xl border-2 border-dashed border-[var(--lab-border)] flex flex-col items-center justify-center gap-4 cursor-pointer hover:bg-white/5 transition-colors"
        onClick={() => setOpen(true)}
      >
        <div className="flex gap-2">
            <kbd className="px-3 py-1.5 rounded-md bg-[var(--lab-surface)] border border-[var(--lab-border)] font-mono text-xs shadow-lg text-[var(--lab-accent)]">⌘</kbd>
            <kbd className="px-3 py-1.5 rounded-md bg-[var(--lab-surface)] border border-[var(--lab-border)] font-mono text-xs shadow-lg text-[var(--lab-accent)]">K</kbd>
        </div>
        <p className="text-xs font-mono text-[var(--lab-dim)] uppercase tracking-[0.2em]">Invoke Navigator</p>
      </div>

      {open && (
        <div className="fixed inset-0 bg-black/80 backdrop-blur-sm grid place-items-center z-[100] p-4">
          <div className="w-full max-w-lg rounded-2xl border border-[var(--lab-border)] bg-[var(--lab-surface)] shadow-2xl overflow-hidden animate-in zoom-in-95 duration-200">
            <div className="p-4 border-b border-[var(--lab-border)] bg-white/5">
              <input
                autoFocus
                value={q}
                onChange={(e) => setQ(e.target.value)}
                placeholder="Search commands..."
                className="w-full bg-transparent outline-none font-mono text-sm text-[var(--lab-text)]"
              />
            </div>
            <div className="p-2">
              {filtered.map((a) => (
                <button
                  key={a.id}
                  className="w-full flex justify-between items-center px-4 py-3 rounded-lg text-sm font-mono text-[var(--lab-dim)] hover:bg-[var(--lab-accent)] hover:text-black transition-colors"
                  onClick={() => setOpen(false)}
                >
                  <span>{a.label}</span>
                  <span className="opacity-50 text-xs">[{a.key}]</span>
                </button>
              ))}
            </div>
          </div>
        </div>
      )}
    </section>
  );
}