"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoTabs() {
  const [tab, setTab] = useState<"a" | "b" | "c">("a");
  const options = [
    { id: "a", label: "Overview" },
    { id: "b", label: "Analytics" },
    { id: "c", label: "Settings" },
  ];

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div className="p-2 rounded-2xl bg-[var(--conv-surface)] border border-[var(--conv-border)]">
        <div className="flex p-1 gap-1">
          {options.map((t) => (
            <button
              key={t.id}
              onClick={(e) => {
                setTab(t.id as any);
                openSpecCard({
                  featureKey: "tabs",
                  title: "Segmented Control",
                  tags: ["navigation", "layout"],
                  codeUrl: "...",
                  anchor: { x: e.clientX, y: e.clientY },
                });
              }}
              className={`flex-1 py-2 text-sm font-medium rounded-xl transition-all ${
                tab === t.id ? "bg-white text-[var(--conv-ink)] shadow-sm" : "text-[var(--conv-dim)] hover:text-[var(--conv-ink)]"
              }`}
            >
              {t.label}
            </button>
          ))}
        </div>
        <div className="p-8 text-center animate-in fade-in zoom-in-95 duration-500">
           <p className="text-[var(--conv-dim)] font-light italic">Viewing {tab.toUpperCase()} Dataset</p>
        </div>
      </div>
    </section>
  );
} 