"use client";
import React from "react";
import type { LikeVariant } from "../likes/LikesContext";
import { useLikes } from "../likes/LikesContext";

type Props = {
  open: boolean;
  onClose: () => void;
  anchor?: { x: number; y: number };
  featureKey: string;
  title: string;
  codeUrl?: string;
  tags?: string[];
};

const VARIANTS: { key: LikeVariant; label: string; emoji: string }[] = [
  { key: "like", label: "I like it", emoji: "✦" },
  { key: "love", label: "Love this", emoji: "♥" },
  { key: "use_it", label: "Put this in my build", emoji: "⚡" },
  { key: "almost", label: "Almost (needs tweak)", emoji: "✎" },
  { key: "not_for_me", label: "Not for me", emoji: "✕" },
];

export function SpecCard({
  open,
  onClose,
  anchor,
  featureKey,
  title,
  codeUrl,
  tags,
}: Props) {
  const { items, setVariant } = useLikes();
  const rec = items[featureKey];
  const isChecked = (k: LikeVariant) => rec?.variants.has(k) ?? false;

  if (!open) return null;

  // Positioning logic
  const ax = anchor?.x ?? 100;
  const ay = anchor?.y ?? 100;
  const w = typeof window !== "undefined" ? window.innerWidth : 1200;
  const h = typeof window !== "undefined" ? window.innerHeight : 800;

  return (
    <div
      className="fixed z-[100] w-[300px] rounded-[2rem] border border-white/40 bg-white/70 backdrop-blur-2xl shadow-[0_20px_50px_rgba(0,0,0,0.15)] animate-in fade-in zoom-in-95 duration-200"
      style={{
        top: Math.min(ay + 12, h - 420),
        left: Math.min(ax - 150, w - 320),
      }}
      role="dialog"
    >
      {/* Top Section / Header */}
      <div className="p-6 pb-4">
        <div className="flex items-start justify-between mb-4">
          <div className="space-y-1">
            <span className="text-[10px] font-bold uppercase tracking-[0.2em] text-zinc-400">
              Component Spec
            </span>
            <h3 className="text-xl font-bold tracking-tighter text-zinc-900">
              {title}
            </h3>
          </div>
          <button
            onClick={onClose}
            className="h-8 w-8 flex items-center justify-center rounded-full bg-zinc-100 text-zinc-500 hover:bg-zinc-900 hover:text-white transition-all"
          >
            ✕
          </button>
        </div>

        {tags?.length ? (
          <div className="flex flex-wrap gap-1.5 mb-4">
            {tags.map((t) => (
              <span
                key={t}
                className="text-[9px] font-bold uppercase tracking-wider px-2 py-0.5 rounded-md bg-zinc-900/5 text-zinc-500 border border-zinc-900/5"
              >
                {t}
              </span>
            ))}
          </div>
        ) : null}

        {codeUrl && (
          <a
            href={codeUrl}
            target="_blank"
            rel="noreferrer"
            className="group flex items-center gap-2 text-xs font-bold text-indigo-600 hover:text-indigo-700 transition-colors"
          >
            <span className="h-1 w-1 rounded-full bg-indigo-600" />
            VIEW SOURCE CODE
            <span className="opacity-0 group-hover:opacity-100 group-hover:translate-x-1 transition-all">
              →
            </span>
          </a>
        )}
      </div>

      {/* Selection Area */}
      <div className="px-3 pb-6 space-y-1">
        <div className="px-3 py-2">
          <span className="text-[10px] font-bold text-zinc-400 uppercase tracking-widest">
            Your Feedback
          </span>
        </div>
        {VARIANTS.map((v) => {
          const active = isChecked(v.key);
          return (
            <button
              key={v.key}
              onClick={() => {
                // Omit undefined properties to satisfy exactOptionalPropertyTypes
                const meta: { codeUrl?: string; tags?: string[] } = {
                  ...(codeUrl ? { codeUrl } : {}),
                  ...(tags && tags.length ? { tags } : {}),
                };
                setVariant(featureKey, title, v.key, !active, meta);
              }}
              className={`w-full flex items-center justify-between px-4 py-3 rounded-2xl text-sm font-medium transition-all ${
                active
                  ? "bg-zinc-900 text-white shadow-lg scale-[1.02]"
                  : "hover:bg-zinc-100 text-zinc-600"
              }`}
            >
              <div className="flex items-center gap-3">
                <span
                  className={`text-[10px] ${
                    active ? "text-zinc-400" : "text-zinc-300"
                  }`}
                >
                  {v.emoji}
                </span>
                {v.label}
              </div>
              {active && (
                <div className="h-1.5 w-1.5 rounded-full bg-indigo-400 animate-pulse" />
              )}
            </button>
          );
        })}
      </div>

      {/* Subtle Footer Glow */}
      <div className="absolute -z-10 inset-0 rounded-[2rem] bg-gradient-to-br from-indigo-500/10 via-transparent to-transparent opacity-50" />
    </div>
  );
}
