"use client";
import React, { useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

type Card = { id: string; label: string };

export function DemoSwipeCards() {
  const [cards, setCards] = useState<Card[]>([
    { id: "1", label: "User Interface" },
    { id: "2", label: "Experience Design" },
    { id: "3", label: "System Architecture" },
  ]);

  const swipe = () => {
    setCards((prev): Card[] => {
      if (prev.length === 0) return prev;
      // Assert non-empty tuple so `first` is a Card, not Card | undefined
      const [first, ...rest] = prev as [Card, ...Card[]];
      return [...rest, first];
    });
  };

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div className="flex items-center justify-between mb-8">
        <h2 className="text-sm font-semibold text-[var(--conv-dim)] uppercase tracking-tight">
          Interactive Sorting
        </h2>
        <button
          className="text-xs text-[var(--conv-accent)] font-medium"
          onClick={(e) =>
            openSpecCard({
              featureKey: "swipe-cards",
              title: "Deck Engine",
              tags: ["Mobile", "Gestures"],
              codeUrl: "https://github.com/your/repo/path/DemoSwipeCards.tsx",
              anchor: { x: e.clientX, y: e.clientY },
            })
          }
        >
          View Component Spec
        </button>
      </div>

      <div className="relative h-[250px] w-full max-w-sm mx-auto">
        {cards
          .slice(0, 3)
          .reverse()
          .map((c: Card, i: number) => (
            <div
              key={c.id}
              className="absolute inset-0 rounded-[2rem] border border-[var(--conv-border)] bg-white shadow-xl flex flex-col items-center justify-center p-8 transition-all duration-500 ease-out"
              style={{
                transform: `translateY(${(2 - i) * 12}px) scale(${0.9 + i * 0.05})`,
                zIndex: i,
                opacity: 0.5 + i * 0.25,
              }}
            >
              <div className="w-12 h-12 rounded-full bg-[var(--conv-surface)] mb-4" />
              <div className="text-xl font-bold text-[var(--conv-ink)]">
                {c.label}
              </div>
            </div>
          ))}
        {cards.length === 0 && (
          <div className="absolute inset-0 flex items-center justify-center text-[var(--conv-dim)] font-mono text-xs">
            DECK_EMPTY
          </div>
        )}
      </div>

      <div className="mt-12 flex justify-center gap-4">
        <button
          className="h-14 w-14 rounded-full border border-[var(--conv-border)] flex items-center justify-center hover:bg-red-50 hover:border-red-200 transition-all active:scale-90"
          onClick={swipe}
        >
          ✕
        </button>
        <button
          className="h-14 w-14 rounded-full border border-[var(--conv-border)] flex items-center justify-center hover:bg-emerald-50 hover:border-emerald-200 transition-all active:scale-90"
          onClick={swipe}
        >
          ✓
        </button>
      </div>
    </section>
  );
}
