"use client";

import React, { useEffect, useState } from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoParallaxHero() {
  const [y, setY] = useState(0);

  useEffect(() => {
    const onScroll = () => setY(window.scrollY);
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  return (
    <section
      className="relative h-[60vh] overflow-hidden rounded-[2.5rem] bg-white border border-zinc-100 shadow-sm mx-auto max-w-6xl group cursor-help"
      onMouseEnter={(e) =>
        openSpecCard({
          featureKey: "parallax-hero",
          title: "ParallaxHero",
          tags: ["motion", "depth", "hero"],
          codeUrl: "https://github.com/your/repo/path/ParallaxHero.tsx",
          anchor: { x: e.clientX, y: e.clientY },
        })
      }
    >
      <div className="absolute inset-0 bg-gradient-to-b from-transparent to-zinc-50/50 z-[1]" />
      <div
        className="absolute inset-0 bg-[url('/vercel.svg')] bg-no-repeat bg-[center_top_20%] bg-[length:400px] opacity-[0.03] grayscale"
        style={{ transform: `translateY(${y * 0.15}px)` }}
      />
      <div
        className="absolute inset-0 bg-[url('/globe.svg')] bg-no-repeat bg-center opacity-[0.05]"
        style={{ transform: `translateY(${y * 0.3}px) scale(1.1)` }}
      />
      
      <div className="relative z-10 h-full flex flex-col items-center justify-center px-6">
        <span className="text-xs uppercase tracking-[0.3em] text-zinc-400 mb-4 transition-all group-hover:tracking-[0.4em] duration-500">
          Immersive Experience
        </span>
        <h2 className="text-4xl md:text-7xl font-light tracking-tighter text-zinc-900">
          Atmospheric <span className="font-serif italic text-zinc-400">Depth</span>
        </h2>
        <p className="text-zinc-500 mt-6 max-w-md text-center leading-relaxed font-light">
          Layered motion that responds to user movement, creating a sense of physical space in a digital medium.
        </p>
      </div>
    </section>
  );
}