"use client";
import React from "react";
import { openSpecCard } from "@/components/spec/SpecCardPortal";

export function DemoCalendarHeatmap() {
  const vals = Array.from({ length: 84 }).map((_, i) => (i * 17) % 5);

  const getOpacity = (v: number) => {
    const levels: Record<number, string> = { 0: "0.1", 1: "0.3", 2: "0.5", 3: "0.7", 4: "1" };
    return levels[v] || "0.1";
  };

  return (
    <section className="mx-auto max-w-5xl px-6">
      <div
        className="rounded-2xl border border-[var(--lab-border)] bg-[var(--lab-surface)] p-6 cursor-pointer hover:border-[var(--lab-dim)] transition-colors"
        onClick={(e) => openSpecCard({
            featureKey: "calendar-heatmap",
            title: "Activity Matrix",
            tags: ["Data-Viz", "Grid", "System"],
            codeUrl: "...",
            anchor: { x: e.clientX, y: e.clientY },
          })
        }
      >
        <div className="flex justify-between items-center mb-6">
          <h3 className="font-mono text-xs uppercase tracking-widest text-[var(--lab-dim)]">Commit_History</h3>
          <div className="flex gap-1">
             {[0, 1, 2, 3, 4].map(v => <div key={v} className="w-2 h-2 rounded-sm bg-[var(--lab-accent)]" style={{ opacity: getOpacity(v) }} />)}
          </div>
        </div>
        <div className="flex flex-wrap gap-1.5">
          {vals.map((v, i) => (
            <div 
              key={i} 
              className="h-3 w-3 md:h-4 md:w-4 rounded-[2px] bg-[var(--lab-accent)] transition-all hover:scale-125 hover:shadow-[0_0_10px_var(--lab-accent)]" 
              style={{ opacity: getOpacity(v) }} 
            />
          ))}
        </div>
      </div>
    </section>
  );
}